<script>
export default {
  data() {
    return {
      activeTab: "HOME",
      SelfImg: "./self.png",
      psdImg: "./psd.png",
      aiImg: "./ai.png",
      figmaImg: "./figma.png",
      vsImg: "./vs.png",
      htmlImg: "./html.png",
      cssImg: "./css.png",
      jsImg: "./js.png",
      vueImg: "./vue.png",
      promotion_thum1: "./lg_1thum.jpg",
      promotion_thum2: "./lg_2thum.jpg",
      promotion_thum3: "./lg_3thum.jpg",
      promotion_thum4: "./lg_4thum.jpg",
      promotion_thum5: "./lg_5thum.jpg",
      promotion_thum6: "./lg_6thum.jpg",
      images: [
        { src: "./lg_1.jpg", alt: "디자인 작업 1" },
        { src: "./lg_2.jpg", alt: "디자인 작업 2" },
        { src: "./lg_3.jpg", alt: "디자인 작업 3" },
        { src: "./lg_4.jpg", alt: "디자인 작업 4" },
        { src: "./lg_5.jpg", alt: "디자인 작업 5" },
        { src: "./lg_6.jpg", alt: "디자인 작업 6" },
      ],
      sections: [
        { id: "section1", tab: "HOME" },
        { id: "section2", tab: "ABOUT" },
        { id: "section3", tab: "SKILL" },
        { id: "section4", tab: "DESIGN" },
        { id: "section5", tab: "ENDING" },
      ],
      isPopupVisible: false,
      currentImage: null,
    };
  },
  methods: {
    changeTab(params) {
      const [event, section] = params;
      this.activeTab = event;
      if (this.$refs[section]) {
        this.$refs[section].scrollIntoView({ behavior: "smooth" });
      }
    },
    Top() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    handleIntersection(entries) {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          const section = this.sections.find(
            (sec) => sec.id === entry.target.id
          );
          if (section) {
            this.activeTab = section.tab;
          }
        }
      });
    },
    openPopup(imageSrc) {
      this.currentImage = imageSrc;
      this.isPopupVisible = true;
    },
    closePopup() {
      this.isPopupVisible = false;
      this.currentImage = null;
    },
  },
  mounted() {
    const observer = new IntersectionObserver(this.handleIntersection, {
      threshold: 0.5,
    });

    this.sections.forEach((section) => {
      const element = this.$refs[section.id];
      if (element) {
        observer.observe(element);
      }
    });

    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    window.onbeforeunload = function () {
      window.scrollTo(0, 0);
    };
  },
};
</script>
